import { getItem, setItem } from '../storage';

//     buyItem(e.target, '1', '.2', '.3', '.4')
//  ''      ,     '.'   '.',   ''
export const buyItem = (target, class1, class2, class3, class4) => {
  if (!target.classList.contains(class1)) return; //         'buy-btn'
  const id = target.closest(class2).dataset.id;  //           '.cart'
  const data = {
    id,
    amount: 1,
  };
  const bucket = getItem('bucket');
  setItem('bucket', [...bucket, data]);
  const buyBtn = target.closest(class3); //       '.buy'
  const checkBtn = target.closest(class4).lastElementChild; //         '.buy-info'
  buyBtn.classList.toggle('hidden');
  checkBtn.classList.toggle('hidden');
};
